.list{
    color: black;
    width: 300px;
    background-color: #FA4EAB;
    
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p{
    padding: 10px; 
    position: relative;
    cursor: pointer; /*hand symbol*/
    font-size: 20px;
    
}
.list p span{
    position: absolute;
    right: 10px; /*now p is positioned i.e as relative. Absolute: element will be positioned relative to the closest 
    positioned ancestor. so with respect to p, span will positioned as right 10px*/

}
.icon{
    margin: 0 5px;
    font-size: 15px;
}
.list p input{
    background-color: transparent;
    border: 0;
    color: black;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}
  





    
    /*
    relative vs absolute:- Absolute: element will be positioned relative to the closest positioned ancestor
    R
    elative: element will be posiitoned relative to its original position
    postition: static , does not allow any offset changes like top,bottom,etc. 
    wherease position: relative allows
    position: relative does not change the layout as it is positioned relative to the parent where position: absolute 
    changes the layout
    */
   

