body {
  background-color: #610C63;
 
  
}

.App{
  background-color: #37E2D5;
  height: 500px;
  width: 400px;
  margin: 40px auto;  /* top and bottom margin is 40px and left and right are: 
                        automatically centered */
}
#form input{
  background-color: rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px; /*top and bottom are 0, right and left are 20px*/
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;  /*to have curved input*/
  color: white;
}
 #form input::placeholder{
  color: rgba(255,255,255,0.5);
 }
 #form button:focus,#form input:focus{
  outline: none; /*to avoid outline when clicking on input text field*/
 }
 #form button{
     height: 50px;
     width: 80px;
     border-radius: 5px;
     border: 0;
     background-color: #694E4E;
     cursor: pointer; /*hand symbol*/
     color: white;

     
     font-weight: bolder;
 }

